.item {
  position: relative;
  width: 100%;
  max-width: 600px;
  background: #ffffff;
  box-sizing: border-box;
  border: 1px solid #aeaeae;
  border-radius: 30px;
  text-align: center;
  box-shadow: 0 0 15px 5px rgba(0,0,0,.4);
  padding: 20px;
  margin: 0 auto 30px;
}

.box {
  display: flex;
  align-items: center;
  justify-content: center;
}

.delete {
  position: absolute;
  top: 10px;
  right: 15px;
  width: 30px;
  height: 30px;
  cursor: pointer;
  -webkit-transition: .3s;
  transition: .3s;
  opacity: 0.5;
}
.delete:hover {
  opacity: 1;
}
.delete:before, .delete:after {
  position: absolute;
  left: 15px;
  content: '';
  height: 30px;
  width: 2px;
  background: #000000;
}
.delete:before {
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}
.delete:after {
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
}

.name {
  font-size: 30px;
  font-weight: 700;
}

.temp {
  height: 50px;
  font-size: 22px;
  line-height: 40px;
}

.box2 {
  display: flex;
  justify-content: space-between;
  max-width: 400px;
  margin: 0 auto;
}

.box2 p {
  font-size: 16px;
  margin: 0;
}

.wrapper {
  width: 340px;
  margin: 0 auto;
}

.add {
  width: 40px;
  height: 40px;
  font-size: 60px;
  line-height: 26px;
  cursor: pointer;
  -webkit-transition: .3s;
  transition: .3s;
  opacity: 0.7;
  margin: 0 auto;
}

.add.active {
  opacity: 1;
  -webkit-transform: rotate(315deg);
          transform: rotate(315deg);
}

.add:hover {
  opacity: 1;
}

form {
  position: relative;
  border-radius: 5px;
  margin-top: 10px;
  -webkit-animation: show .3s;
          animation: show .3s;
}

@-webkit-keyframes show {
  0% {
    -webkit-transform: translateY(20px);
            transform: translateY(20px);
    opacity: 0;
  }
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    opacity: 1;
  }
}

@keyframes show {
  0% {
    -webkit-transform: translateY(20px);
            transform: translateY(20px);
    opacity: 0;
  }
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    opacity: 1;
  }
}

input {
  width: 100%;
  height: 44px;
  box-sizing: border-box;
  border-radius: 10px;
  border: 2px solid #aeaeae;
  padding: 5px 10px;
  outline: none;
  -webkit-transition: 0.3s;
  transition: 0.3s;
  font-size: 16px;
}

input:hover, input:focus {
  box-shadow: 0 5px 15px -5px rgba(0,0,0,.4);
  border: 2px solid #000;
}

button {
  position: absolute;
  top: 2px;
  right: 2px;
  height: 40px;
  border: 0;
  background: #1E90FF;
  border-radius: 0 8px 8px 0;
  padding: 0 10px;
  color: #ffffff;
  font-size: 14px;
  cursor: pointer;
  outline: none;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}

button:disabled {
  background: #aeaeae;
  color: #000000;
  cursor: no-drop;
}
.container {
  max-width: 600px;
  box-sizing: border-box;
  padding: 0 15px;
  margin: 4% auto 0;
}

.error {
  position: relative;
  width: 235px;
  background: #ffffff;
  padding: 11px;
  border-radius: 35px;
  color: red;
  font-size: 18px;
  text-align: center;
  margin: 15px auto 0;
  box-shadow: 0 5px 12px 5px rgba(0,0,0,.3);
}
.error:before {
  content: '';
  position: absolute;
  top: -23px;
  left: 30px;
  border: 10px solid transparent;
  border-bottom: 20px solid #ffffff;
}

body {
  background: url(/weather-app/static/media/img.371b775d.jpg) no-repeat;
}

.loading {
  position: absolute;
  top: 10%;
  left: 0;
  right: 0;
  width: 70px;
  height: 70px;
  border-radius: 100%;
  background: url(https://mir-s3-cdn-cf.behance.net/project_modules/disp/93f32913765711.56277dab54b52.gif) center no-repeat;
  margin: 0 auto;
}
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

